@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 400;
  font-display: swap;
  src: url('../vendor/fonts/Inter-Regular.woff2') format('woff2'),
       url('../vendor/fonts/Inter-Regular.woff') format('woff'),
       url('../vendor/fonts/Inter-Regular.ttf') format('truetype'),
       url('../vendor/fonts/Inter-Regular.eot') format('eot');
}

